  margin: 0;
}
body {
  font-family: "Work Sans", "Helvetica Neue", Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}
a {
  text-decoration: none;
}
#app {
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  padding: 24px;
}



